export default {
  name: 'resultList',
  props: ['languageRes', 'transResult'],
  methods: {
    transBack(index) {
      this.$emit('transBack', index)
    },
    paste(index) {
      wx.vibrateShort()
      uni.setClipboardData({
        data: this.transResult[index],
        success: function () {
          console.log('success');
        }
      });
    }
  }
}
